/* ------------------------------------------------------------------------ */
.loader {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  align-items: center;
  --duration: 0.50s;
  font-weight: bold;
  user-select: none;
  white-space: nowrap;
  z-index: 1;
  gap: 10px;
  background-color: rgba(255, 255, 255, 0.7);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}
.loader > .msg {
  font-size: 20px;
}
.loader > .icon {
  display: flex;
  gap: 5px;
}
.loader > .icon > * {
  display: block;
  width: 10px;
  height: 30px;
  animation: loading-rect var(--duration) infinite ease-in-out;
  background: black;
}
.loader > .icon > *:nth-child(1) {
  animation-delay: 0;
}
.loader > .icon > *:nth-child(2) {
  animation-delay: calc(var(--duration) / 2 * 1 / 3 * -1);
}
.loader > .icon > *:nth-child(3) {
  animation-delay: calc(var(--duration) / 2 * 2 / 3 * -1);
}

table.files {
  font-family: monospace;
  font-size: 11px;
  word-break: break-all;
}
table.files svg {
  fill: currentColor;
  opacity: 0.6;
  width: 1em;
  height: 1em;
  display: inline-block;
  flex: none;
}
table.files td {
  position: relative;
}
table.files .percent-bar {
  height: 100%;
  position: absolute;
  width: 100%;
  inset: 0;
  background: #ccc;
}
table.files .percent-bar > div {
  background: #fff;
  height: 100%;
}
table.files .percent-bar > span {
  inset: 0;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}
table.files tr.open svg.arrow {
  transform: rotate(90deg);
}
table.files tr td:not(:nth-child(1)) {
  white-space: nowrap;
  width: 0;
}

@keyframes loading-rect {
  0%, 100% {
    transform: scaleY(0.2);
    opacity: 0.2;
  }
  50% {
    transform: scaleY(1);
    opacity: 1;
  }
}
.drawer {
  flex-direction: column;
  border: var(--border);
  gap: 0;
  max-width: 100%;
  /* &:after, &:before {
  	content: "";
  	flex: 0 0 0;
  	margin: auto;
  } */
}
.drawer > .header {
  display: flex;
  align-items: center;
  background: #ddd;
  border-bottom: var(--border);
  cursor: pointer;
}
.drawer > .header > .inner {
  overflow: hidden;
  padding: 5px;
  box-sizing: content-box;
  font-size: 1.3em;
  font-weight: 500;
  position: relative;
  user-select: none;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 5px;
  flex: 1;
}
.drawer > .header > .inner > *:nth-child(1) {
  display: flex;
  flex: 1 0;
}
.drawer[data-collapsible] .collapse-arrow {
  transition: all 0.2s;
  pointer-events: none;
  width: 30px;
  text-align: center;
}
.drawer > .body {
  display: flex;
  padding: 5px;
  gap: 5px;
  flex-direction: column;
}
.drawer:not([data-collapsible]) > .header {
  cursor: default;
}
.drawer:not([data-collapsible]) .collapse-arrow {
  display: none;
}
.drawer.hide > .header {
  border-bottom-width: 0;
}
.drawer.hide .collapse-arrow {
  transform: rotate(180deg);
}
.drawer.hide > .body {
  display: none;
}

#livestreamer {
  display: flex;
  height: 100%;
  flex-direction: row;
}
#livestreamer > .main {
  width: 100%;
  z-index: 1;
  padding: var(--main-padding);
  background: var(--background-color);
  display: block;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: auto;
}
#livestreamer > .help {
  border-bottom: var(--border);
  display: none;
  background: white;
  position: sticky;
  top: 0;
  width: var(--help-size);
  height: 100vh;
  z-index: 2;
}
body.show-side-panel #livestreamer > .main {
  width: calc(100% - var(--help-size));
}
body.show-side-panel #livestreamer > .help {
  display: block;
}

@media all and (max-width: 640px) {
  body.show-side-panel #livestreamer {
    flex-direction: column;
  }
  body.show-side-panel #livestreamer > .main {
    order: 1;
    width: 100%;
    min-height: auto;
    height: calc(100% - var(--help-size));
  }
  body.show-side-panel #livestreamer > .help {
    order: 0;
    border-left: none;
    border-top: var(--border);
    width: 100%;
    height: var(--help-size);
  }
}
