.test-stream {
  display: flex;
  flex-direction: column;
  background: #111;
  position: relative;
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 0;
  padding: 0;
  inset: 0;
}
.test-stream .play {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.3);
  color: white;
  cursor: pointer;
  font-size: 40px;
}
.test-stream .play > * {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  opacity: 0.5;
}
.test-stream .video-wrapper {
  display: flex;
  width: 100%;
  height: 100%;
  inset: 0;
  position: absolute;
}
.test-stream .video-wrapper video {
  width: 100%;
  height: 100%;
}
.test-stream video:focus {
  outline: none !important;
}
.test-stream .overlay {
  position: absolute;
  inset: 0;
  opacity: 0;
  transition: all 0.4s;
  z-index: 999;
  pointer-events: none;
}
.test-stream .overlay > * {
  pointer-events: auto;
}
.test-stream .info {
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  color: white;
  padding: 3px 5px;
  text-align: right;
  z-index: 999;
}
.test-stream .buttons {
  position: absolute;
  display: flex;
  top: 10px;
  left: 10px;
}
.test-stream:hover .play > * {
  opacity: 1;
  transform: scale(1.2);
}
.test-stream:hover .overlay {
  opacity: 1;
}

