}

/* ------------------------------------------ */
.ui-resizable {
  overflow: hidden;
}

.ui-resizable-n {
  height: 7px;
  top: 0;
}

.ui-resizable-s {
  height: 7px;
  bottom: 0;
}

.ui-resizable-e {
  width: 7px;
  right: 0;
}

.ui-resizable-w {
  width: 7px;
  left: 0;
}

.ui-resizable-handle {
  background: #ddd;
  position: relative;
}
.ui-resizable-handle:after {
  content: "";
  display: block;
  position: absolute;
}

.ui-resizable-s:after, .ui-resizable-n:after {
  border-top: 1px solid var(--border-color);
  top: 3px;
  width: 50%;
  transform: translateX(-50%);
  left: 50%;
}

.ui-resizable-w:after, .ui-resizable-e:after {
  border-left: 1px solid var(--border-color);
  left: 3px;
  height: 50%;
  transform: translateY(-50%);
  top: 50%;
}

