  position: absolute;
  width: 100%;
  border-left: 1px solid rgb(134, 138, 170);
  height: 5px;
}
.seek-wrapper > .seek > .chapters > *:after {
  content: "";
  position: absolute;
  background: rgb(134, 138, 170);
  height: 0;
  transition: all 0.1s;
  width: 100%;
}
.seek-wrapper > .seek > .chapters > *.hover:after {
  height: 5px;
}
.seek-wrapper > .seek > .chapters > *:first-child:before {
  border-left: none;
}
.seek-wrapper > .seek > .ranges {
  pointer-events: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  height: 2px;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.5;
  overflow: hidden;
}
.seek-wrapper > .seek > .ranges > * {
  position: absolute;
  background: rgb(75, 179, 72);
  height: 100%;
}
.seek-wrapper > .seek > .bar {
  pointer-events: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  width: 0;
  background: #fff;
  /* &:after {
  	content:"";
  	display: block;
  	height:100%;
  	width:2px;
  	background:rgb(85, 85, 85);
  	right:-1px;
  	position:absolute;
  	transition: all 0.5s;
  	opacity:0;
  } */
}
.seek-wrapper > .seek:not([disabled]):hover .bar:after {
  opacity: 1;
}

.ticks-bar .cursor {
  position: absolute;
  top: 0;
  bottom: 0;
  pointer-events: none;
  border-left: 1px solid rgba(0, 0, 0, 0.4);
}
.ticks-bar:not(.hover) .seek-time, .ticks-bar.no-duration .seek-time {
  display: none;
}
.ticks-bar:not(.hover) .cursor, .ticks-bar.no-duration .cursor {
  display: none;
}
.ticks-bar .ticks {
  user-select: none;
  font-family: monospace;
  color: rgba(0, 0, 0, 0.4);
  font-size: 11px;
}
.ticks-bar .ticks > * {
  pointer-events: none;
  position: absolute;
  bottom: 0;
  background: #aaa;
  width: 1px;
  height: 100%;
}
.ticks-bar .ticks > * > * {
  position: absolute;
  left: 2px;
  bottom: 0;
  white-space: nowrap;
}
.ticks-bar[data-placement=top] .ticks > * {
  bottom: none;
  top: 0;
}
.ticks-bar[data-placement=top] .ticks > * > * {
  bottom: none;
  top: 0;
}

.tri {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
}
.tri.top-left {
  right: 1px;
  top: 0;
  border-width: 0 var(--triangle-size) var(--triangle-size) 0;
  border-color: transparent var(--color) transparent transparent;
}
.tri.top-right {
  left: 1px;
  top: 0;
  border-width: var(--triangle-size) var(--triangle-size) 0 0;
  border-color: var(--color) transparent transparent transparent;
}
.tri.bottom-left {
  right: 1px;
  bottom: 0;
  border-width: 0 0 var(--triangle-size) var(--triangle-size);
  border-color: transparent transparent var(--color) transparent;
}
.tri.bottom-right {
  left: 1px;
  bottom: 0;
  border-width: var(--triangle-size) 0 0 var(--triangle-size);
  border-color: transparent transparent transparent var(--color);
}

.noUi-handle {
  border: 1px solid #bbb;
}

[disabled] .noUi-connect {
  cursor: not-allowed;
}

table.keyboard-shortcuts th {
  text-align: center;
}
table.keyboard-shortcuts td {
  text-align: center;
  width: auto;
}
table.keyboard-shortcuts td:first-child {
  text-align: center;
  font-weight: bold;
  font-family: sans-serif;
}

