/* ------------------------------------------ */
.fancybox__container {
  --fancybox-bg: rgba(0,0,0, 0.7) !important;
}

.fancybox__slide {
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
  padding-top: 48px !important;
}

.fancybox__content {
  --min-width: 300px;
  --max-width: 100%;
  width: 50%;
  width: max(50%, 600px);
  min-width: var(--min-width);
  max-width: var(--max-width);
  overflow-wrap: break-word;
  padding: 15px;
}
.fancybox__content .modal-header {
  display: flex;
  font-size: 20px;
  border-bottom: 1px solid #ddd;
  padding: 15px;
}
.fancybox__content .modal-header.overflow {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.fancybox__content .modal-header.overflow * {
  overflow: hidden;
  text-overflow: ellipsis;
}
.fancybox__content .modal-content {
  padding: 15px;
}
.fancybox__content .modal-footer {
  display: flex;
  flex: 1;
  border-top: 1px solid #ddd;
  padding: 15px;
}
.fancybox__content .modal-footer > *:not(:last-child) {
  margin-right: 10px;
}

.uis.bar {
  background: #ddd;
  border: var(--border);
  position: relative;
  height: var(--input-height);
  padding: 5px;
  overflow: hidden;
}
.uis.bar .inner {
  position: absolute;
  top: 0;
  height: 100%;
  background: white;
  inset: 0;
}
.uis.bar .text {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.uis.process {
  padding: 5px;
  border: var(--border);
}

figure {
  text-align: center;
  background-color: #222;
  margin: 0;
}
figure figcaption {
  color: #fff;
  padding: 5px;
  font-size: 80%;
}